import React, { Component } from "react";
import {
  Tabs,
  Tab,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button,
  CardMenu,
  IconButton,
} from "react-mdl";

class Project extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  toggleCategories() {
    if (this.state.activeTab === 0) {
      return (
        <div className="projects-grid">
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#000000",
                height: "176px",
                background:
                  "url(https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1024px-React-icon.svg.png) center/cover",
              }}
            >
              Expense Tracker
            </CardTitle>
            <CardText>
              Untuk melacak pemasukan dan pengeluaran
            </CardText>
            <CardActions border>
              <Button colored>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href="https://github.com/raihanputraramd/Expense-tracker"
                >
                  Github
                </a>
                </Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 1) {
      return (
        <div className="projects-grid">
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#000000",
                height: "176px",
                background:
                  "url(data:image/png;base64,) center/cover",
              }}
            >
              Extra
            </CardTitle>
            <CardText>Web Untuk Mengatur Ekstrakulikuler Sekolah</CardText>
            <CardActions border>
              <Button colored>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href="https://github.com/E-X-TRA/extra"
                >
                  Github
                </a>
              </Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    } else if (this.state.activeTab === 2) {
      return (
        <div className="projects-grid">
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#000000",
                height: "176px",
                background:
                  "url(data:image/png;base64,) center/cover",
              }}
            >
              Cyclyx
            </CardTitle>
            <CardText>
              Aplikasi yang menyediakan kebutuhan bersepeda anda
            </CardText>
            <CardActions border>
              <Button colored>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href="https://github.com/E-X-TRA/Cyclyx"
                >
                  Github
                </a>
              </Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
          <Card shadow={5} style={{ minWidth: "450", margin: "auto" }}>
            <CardTitle
              style={{
                color: "#000000",
                height: "176px",
                background:
                  "url(data:image/png;base64,) center/cover",
              }}
            >
              Auktion
            </CardTitle>
            <CardText>Aplikasi untuk melakukan Lelang</CardText>
            <CardActions border>
              <Button colored>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  href="https://github.com/raihanputraramd/Auktion"
                >
                  Github
                </a>
              </Button>
            </CardActions>
            <CardMenu style={{ color: "#fff" }}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    }
  }

  render() {
    return (
      <div className="category-tabs">
        <Tabs
          activeTab={this.state.activeTab}
          onChange={(tabId) => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>React.js</Tab>
          <Tab>HTML/CSS</Tab>
          <Tab>Android</Tab>
        </Tabs>

        <Grid>
          <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Project;
